.message {
    overflow-x: hidden;
    position: relative;

}

#ysx-message>div{
    width: 100%;
    height: 100%;
}

.rightMoveMessage {
    width: 100%;
    position: absolute;
    top: 80px;
    left: 187px;
    -webkit-transition: all 1.5s;
    -o-transition: all 1.5s;
    transition: all 1.5s;
    padding-top: 10px;
}

.ysx-message {
    width: 100%;
    position: absolute;
    top: 80px;
    left: 0;
    -webkit-transition: all 1.5s;
    -o-transition: all 1.5s;
    transition: all 1.5s;
    padding-top: 10px;
}

